.md-modal-container.ember-modal-dialog {
  width: 350px;
  z-index: 2100;

  h1 {
    color: $brand-info;
    font-size: 1.2em;
    margin-bottom: 1em;
  }

  .md-modal-buttons {
    margin-top: 10px;
  }

  .md-modal-body {
    min-height: 75px;
    padding: 10px;
  }

  .alert {
    font-size: 1.2em;
    font-weight: 700;
  }
}

.md-modal-overlay.translucent {
  background-color: rgba($gray-lighter, .85);
  z-index: 2000;
}

.md-modal-close {
  background-color: $body-bg;
  border-radius: 14px;
  border-style: none;
  box-shadow: 0 0 10px;
  color: $gray-dark;
  font-size: 14px;
  opacity: .8;
  padding: 1px;
  position: absolute;
  right: -1em;
  top: -1em;
  transition: color 250ms;

  &:hover {
    color: $brand-danger;
    opacity: 1;
  }
}
